import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { DndProvider, useDrag, useDrop } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { TouchBackend } from "react-dnd-touch-backend";
import { isMobile } from "react-device-detect";

import image9 from "../images/Image9.jpeg";

const ROWS = 4;
const COLS = 4;

const PuzzleGame = ({ imageUrl }) => {
  const [containerSize, setContainerSize] = useState(400);
  const [pieces, setPieces] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const updateSize = () => {
      const newSize = Math.min(window.innerWidth * 0.9, 400);
      setContainerSize(newSize);
    };
    updateSize();
    window.addEventListener("resize", updateSize);
    return () => window.removeEventListener("resize", updateSize);
  }, []);

  const PIECE_SIZE = containerSize / COLS;

  const splitImage = (imageUrl) => {
    const pieces = [];
    for (let row = 0; row < ROWS; row++) {
      for (let col = 0; col < COLS; col++) {
        pieces.push({
          id: `${row}-${col}`,
          src: imageUrl,
          correctIndex: row * COLS + col,
          currentIndex: row * COLS + col,
          left: col * PIECE_SIZE,
          top: row * PIECE_SIZE,
        });
      }
    }
    return pieces;
  };

  useEffect(() => {
    setPieces(shuffleArray(splitImage(imageUrl)));
  }, [imageUrl, PIECE_SIZE]);

  const shuffleArray = (array) => {
    let shuffled = [...array];
    for (let i = shuffled.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
    }
    return shuffled.map((piece, index) => ({ ...piece, currentIndex: index }));
  };

  const movePiece = (fromIndex, toIndex) => {
    const newPieces = [...pieces];

    newPieces[fromIndex] = { ...newPieces[fromIndex], currentIndex: toIndex };
    newPieces[toIndex] = { ...newPieces[toIndex], currentIndex: fromIndex };

    [newPieces[fromIndex], newPieces[toIndex]] = [
      newPieces[toIndex],
      newPieces[fromIndex],
    ];

    setPieces(newPieces);
  };

  useEffect(() => {
    const isSolved = pieces.every(
      (piece) => piece.currentIndex === piece.correctIndex
    );
    if (isSolved && pieces.length > 0) {
      setTimeout(() => navigate("/sky-page"), 500);
    }
  }, [pieces, navigate]);

  return (
    <div
      style={{
        display: "flex",
        height: "100vh",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
      }}
    >
      <h2 style={{ textAlign: "center", maxWidth: "460px", color: "#fff" }}>
           ,      
        .    ,     .
      </h2>
      <div
        style={{
          position: "relative",
          width: `${containerSize}px`,
          height: `${containerSize}px`,
        }}
      >
        {pieces.map((piece, index) => (
          <PuzzlePiece
            key={piece.id}
            index={index}
            piece={piece}
            movePiece={movePiece}
            pieceSize={PIECE_SIZE}
          />
        ))}
      </div>
    </div>
  );
};

const PuzzlePiece = ({ piece, index, movePiece, pieceSize }) => {
  const [{ isDragging }, drag] = useDrag({
    type: "PIECE",
    item: { index },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  });

  const [, drop] = useDrop({
    accept: "PIECE",
    drop: (draggedItem) => {
      movePiece(draggedItem.index, index);
    },
  });

  const row = Math.floor(piece.currentIndex / COLS);
  const col = piece.currentIndex % COLS;

  const style = {
    position: "absolute",
    top: row * pieceSize,
    left: col * pieceSize,
    width: pieceSize,
    height: pieceSize,
    backgroundImage: `url(${piece.src})`,
    backgroundSize: `${COLS * pieceSize}px ${ROWS * pieceSize}px`,
    backgroundPosition: `-${piece.left}px -${piece.top}px`,
    backgroundRepeat: "no-repeat",
    border: "1px solid #000",
    cursor: "grab",
    opacity: isDragging ? 0.5 : 1,
    touchAction: "none",
  };

  return <div ref={(node) => drag(drop(node))} style={style} />;
};

const App = () => {
  return (
    <DndProvider backend={isMobile ? TouchBackend : HTML5Backend}>
      <PuzzleGame imageUrl={image9} />
    </DndProvider>
  );
};

export default App;
