import React, { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  useNavigate,
} from "react-router-dom";

import Puzzle from "./components/puzzle";
import SkyPage from "./pages/Sky";

import BgImage from "./images/BG.jpg";
import ImageRofl from "./images/image-rofl.png";

const PuzzlePage = () => {
  return (
    <div style={styles.container}>
      <Puzzle />
    </div>
  );
};

const BlackScreen = () => {
  return (
    <div
      style={{
        backgroundColor: "#3f3d3d",
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <h2
        style={{
          padding: "0px",
          margin: "0px",
          color: "#fff",
          textAlign: "center",
          marginBottom: "40px",
        }}
      >
           
      </h2>
      <img style={{ maxWidth: "300px" }} src={ImageRofl} alt="" />
    </div>
  );
};

const StartScreen = () => {
  const navigate = useNavigate();
  const [isFirstLaunch, setIsFirstLaunch] = useState(true);

  useEffect(() => {
    const checkFirstLaunch = () => {
      const firstLaunch = localStorage.getItem("firstLaunch");
      if (firstLaunch) {
        setIsFirstLaunch(false);
      } else {
        localStorage.setItem("firstLaunch", "false");
      }
    };

    checkFirstLaunch();
  }, []);

  if (isFirstLaunch) {
    return null;
  }

  return (
    <div style={styles.container}>
      <div style={styles.bottom}>
        <div style={styles.paper}>
          <h1 style={{ textAlign: "center", marginBottom: "90px" }}>
                ?
          </h1>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <button style={styles.button} onClick={() => navigate("/puzzle")}>
              <b></b>
            </button>
            <button style={styles.button} onClick={() => navigate("/black")}>
              <b></b>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

const styles = {
  container: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
    backgroundImage: `url(${BgImage})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    overflow: "hidden",
  },

  bottom: {
    backgroundColor: "#A4231F",
    height: "610px",
    maxWidth: "450px",
    transform: "rotate(-7.5deg)",
    width: "100%",
    margin: "2rem auto",
  },

  paper: {
    backgroundColor: "#FFF",
    boxShadow: "-1px 3px 3px 3px #222",
    height: "480px",
    maxWidth: "350px",
    lineHeight: "1",
    padding: "3em",
    position: "relative",
    textTransform: "uppercase",
    transform: "rotate(7.5deg)",
    zIndex: "10px",
    margin: "2rem auto",
  },

  button: {
    backgroundColor: "#FF69B4",
    padding: "12px 24px",
    minWidth: "100px",
    margin: "10px",
    border: "none",
    color: "white",
    fontSize: "16px",
    cursor: "pointer",
  },
};

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<StartScreen />} />
        <Route path="/puzzle" element={<PuzzlePage />} />
        <Route path="/black" element={<BlackScreen />} />
        <Route path="/sky-page" element={<SkyPage />} />
      </Routes>
    </Router>
  );
};

export default App;
